* {
  font-family: "Raleway", sans-serif;
  box-sizing: border-box;
}

.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding-bottom: 1%;
  padding-top: 1%;
}
.header form {
  width: 30%;
  border: 1px solid #E4E7EE;
  background: #FFFFFF;
  border-radius: 8px;
}
.header form #text {
  height: 30px;
  border: none;
  width: 80%;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  font-style: normal;
  font-weight: 539;
  font-size: 16px;
  color: #6C757D;
}
.header form #submit {
  height: 30px;
  border: none;
  width: 20%;
  border-radius: 0px 8px 8px 0px;
  background: #0D63F3;
  font-style: normal;
  font-weight: 539;
  font-size: 16px;
  color: white;
}
.header .header-btns-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 10%;
}
.header .header-btns-wrapper .header-btns {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ECF4FF;
  border-radius: 8px;
  height: 30px;
  width: 30%;
}
.header button {
  background: linear-gradient(270deg, #4D5EF6 0%, #F64D4D 100%);
  border: none;
  border-radius: 8px;
  width: 5%;
  height: 30px;
}